html {
  height: 100%;
}

body {
  font-family: "Public Sans", sans-serif;
  height: 100%;
}

.site {
  position: relative;
  min-height: 100%;
  padding-bottom: 3em;
}

/**************** 
      Typography 
****************/
h1, .h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
  font-family: "Public Sans", sans-serif;
  color: #0d1267;
  margin-bottom: 0.3em;
  line-height: 1.2;
}

h1, .h1 {
  font-size: clamp(2.375rem, 4vw, 2.75rem);
  font-weight: 400;
  letter-spacing: -1px;
}

h2, .h2 {
  font-size: clamp(1.875rem, 3vw, 2.25rem);
  font-weight: 500;
}

h3, .h3 {
  font-size: clamp(1.375rem, 2vw, 1.875rem);
  font-weight: 400;
}

h4, .h4 {
  font-size: clamp(1.125rem, 2vw, 1.25rem);
  font-weight: 600;
}

p,
li {
  font-size: clamp(1rem, 2vw, 1.125rem);
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
}

p {
  margin-bottom: 1.5em;
}

li {
  margin-bottom: 0.75em;
}

:is(p, ol, ul) + :is(h2, .h2, h3, .h3, h4, .h4) {
  margin-top: 1.25em;
}

a {
  font-size: inherit;
  font-weight: inherit;
  color: #0d1267;
  text-decoration: none;
}
a:hover, a:active {
  text-decoration: 2px underline #fca311;
}

figcaption {
  text-align: center;
  font-size: 0.875rem;
  color: #6c757d;
}

/**************** 
      Buttons 
****************/
.button, #main-nav .navbar__button, .button--sec, .button--pry {
  appearance: none;
  display: inline-flex;
  align-items: center;
  padding: 0.75em 1em;
  border-radius: 0.5em;
  text-decoration: none;
  font-weight: 600;
  font-size: clamp(1rem, 2vw, 1.125rem);
  color: #fff;
  cursor: pointer;
}
.button:hover, #main-nav .navbar__button:hover, .button--sec:hover, .button--pry:hover {
  text-decoration: none;
}
.button--pry {
  text-shadow: 0 0px 3px rgba(0, 0, 0, 0.3);
  border: 2px solid #fca311;
  background-color: #fca311;
  background: linear-gradient(#ffb800, #fca311);
}
.button--pry:hover {
  background: linear-gradient(#fca311, #fca311);
  color: #fff;
}
.button--sec {
  color: #0d1267;
  border: 2px solid #fca311;
  background-color: #fff;
  transition: all 200ms ease-in-out;
}
.button--sec:hover {
  background-color: #fca311;
  text-shadow: 0 0px 3px rgba(0, 0, 0, 0.3);
  color: #fff;
}
.button__share, .button__share--em, .button__share--li, .button__share--tw, .button__share--wa, .button__share--fb {
  display: inline-flex;
  align-items: center;
  padding: 0.5em 0.75em;
  border-radius: 0.5em;
  color: #fff;
  font-size: clamp(0.875rem, 2vw, 1rem);
  text-decoration: none;
  transition: all 300ms ease-in-out;
}
.button__share:hover, .button__share--em:hover, .button__share--li:hover, .button__share--tw:hover, .button__share--wa:hover, .button__share--fb:hover {
  opacity: 0.8;
  text-decoration: none;
  color: #fff;
}
.button__share--fb {
  background-color: #3b5998;
}
.button__share--wa {
  background-color: #25d366;
}
.button__share--tw {
  background-color: #26a7de;
}
.button__share--li {
  background-color: #0077b5;
}
.button__share--em {
  background-color: #dd5143;
}

/**************** 
      WP Blocks 
****************/
.wp-block-quote {
  border-left: 6px solid #0d1267;
  padding-left: 1em;
  margin-bottom: 1.5em;
}

.wp-block-pullquote {
  border-top: 4px solid #e9ecef;
  border-bottom: 4px solid #e9ecef;
  max-width: 480px;
  margin: 0 auto 1.5em;
}
.wp-block-pullquote cite {
  text-transform: uppercase;
  font-size: 12px;
  font-style: normal;
  letter-spacing: 1px;
  position: static;
}

