/**************** 
      Header/Navbar 
****************/
#wrapper-navbar {
  position: sticky;
  top: 0;
  z-index: 1000;
}

#main-nav .navbar-brand {
  max-width: 180px;
}
#main-nav .nav-item {
  margin-right: 3vw;
  margin-bottom: 0;
  color: #0d1267;
  font-weight: 600;
  line-height: 1;
}
#main-nav .dropdown-menu li {
  margin-right: 0;
}
#main-nav .dropdown-item {
  padding: 0.75em 1em;
}
#main-nav .current-menu-item a {
  text-decoration: 2px underline #fca311;
}
#main-nav .social-icons {
  display: flex;
  gap: 2.5vw;
}
#main-nav .navbar__button {
  color: #0d1267;
  border: none;
  border: 1px solid transparent;
  display: none;
}
#main-nav .navbar__button:hover, #main-nav .navbar__button:active {
  border-color: #0d1267;
}
#main-nav .mobile_menu {
  background-color: #0d1267;
  color: #fff;
  width: 100%;
  margin-top: 1em;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 74%;
  transform: scaleY(0);
  transform-origin: top center;
  transition: transform 200ms ease-in-out, opacity 200ms ease;
}
#main-nav .mobile_menu__links {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 0;
  margin-top: 1em;
  padding: 0;
}
#main-nav .mobile_menu__link {
  color: #fff;
}
#main-nav .mobile_menu.active {
  opacity: 1;
  pointer-events: all;
  transform: scaleY(1);
}

@media (max-width: 992px) {
  #main-nav .navbar-brand {
    max-width: 150px;
  }
  #main-nav .social-icons {
    margin-left: auto;
    margin-right: 1.25em;
  }
  #main-nav .social-icons :not(:first-child) {
    display: none;
  }
  #main-nav .navbar__button {
    display: inline-flex;
  }
}
