.posts {
  /* Form & Loading Animation */
}
.posts__header {
  background: linear-gradient(90deg, #fff, #e5e5e5);
  text-align: center;
  padding: 2.5em 1em;
}
.posts__header h1, .posts__header .h1 {
  margin-bottom: 20px;
  font-weight: 600;
}
.posts__header h1 span, .posts__header .h1 span {
  color: #fca311;
}
.posts__header p {
  max-width: 400px;
  margin: auto;
}
.posts__form {
  position: relative;
  width: 100%;
  max-width: 400px;
  margin-left: auto;
}
.posts .loader {
  opacity: 0;
  display: flex;
  justify-content: center;
  gap: 3px;
  width: fit-content;
  transition: opacity 0.3s ease-in;
  position: absolute;
  top: 26px;
  right: 16px;
}
.posts .loader__dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #0d1267;
  animation: bounce 500ms ease-in infinite;
}
.posts .loader__dot:nth-of-type(2) {
  animation-delay: 100ms;
  background-color: #fca311;
}
.posts .loader__dot:nth-of-type(3) {
  animation-delay: 200ms;
}
.posts .loader.show {
  opacity: 1;
}
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
  100% {
    transform: translateY(0);
  }
}
.posts__search {
  width: 100%;
  border: none;
  background: transparent;
  margin: 0.5em auto;
}
.posts__search::placeholder {
  text-align: center;
}
.posts__search:focus {
  outline: 1px solid #0d1267;
}
.posts__search:not(:placeholder-shown) {
  outline: 1px solid #0d1267;
  background: #f5f5ff;
}
.posts__search-results {
  box-shadow: 0 4px 8px #00000033;
  width: 100%;
  height: fit-content;
  max-height: 200px;
  position: absolute;
  top: 100%;
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  background-color: #fff;
  overflow-x: hidden;
}
.posts__search-results__link, .posts__search-results__empty {
  width: 100%;
